export const serviceData = {"activityCaption":"活动问题","activityContent":"### Q1: 我是新用户，为什么不能享受新用户下单立减优惠／新用户首单红包？\n\n* 新用户是指您的下单设备、手机号、饿了么账号、支付账号都是第一次在饿了么平台使用。以上任意条件不满足，都不能享受新用户优惠。\n* 新用户立减优惠、新用户红包、满减特价菜活动不能同时使用。在您首次下单过程中，我们会优先为您选择优惠最大（优惠金额最高）的。\n\n### Q2: 商品活动规则\n\n* 新用户下首单时，如果用户自己主动退单，则下次不可再享受新用户优惠，如果是商家或客服取消订单，则可以再次享受新用户优惠。\n* 5.10版本（含）之前的所有版本，用户在下单时不再享受任何优惠（包含新用户、在线满减等）。\n* 移动端所有活动，相同手机号或设备每天只限3单，超过3单收取原价。\n* 每个订单最多享受3份活动优惠，超过3份的其余商品收取原价。\n* 下单手机号和所选择下单的商家不在同一城市，不享受优惠活动。\n* 配送费不计入满减活动的门槛。\n\n### Q3: 我第一次在 \"A\" 商家下单，\"A\" 商家有参加「新用户」活动，我为什么享受不到新用户的立减优惠？\n\n* 活动中的新用户指的是饿了么网站的新用户，而不是指 \"A\" 商家的新客户。如果您不符合饿了么网站新用户的定义则无法享受于新用户相关的活动优惠。\n","anonymousBuyCaption":"匿名购买","anonymousBuyContent":"![anonymous buy](https://fuss10.elemecdn.com/f/08/bb0bcfc2a10e26524633e38ea47f6jpeg.jpeg)","balanceCaption":"余额问题","balanceContent":"### Q1: 使用余额的条件\n\n为了保护账户安全，使用余额前必须先绑定手机号。\n\n### Q2: 余额可以怎么用？\n\n余额可以在饿了么平台上提现，当余额大于等于待支付金额时可以在支持在线支付的商家中进行消费。提现功能将于2016年12月25日00:00全面开放。\n\n### Q3:我要如何提现？\n\n为了保护账户和资金安全，您在提现前需要输入真实姓名和用该姓名开通的银行卡号、选择开户行，并验证已绑定饿了么账号的手机号。每日只能提现1次，每次限额50元。若提现金额超过50元，点击【提现】时系统会提示您已超额，请您修改提现金额。\n\n### Q4:为什么会提现失败？\n\n可能原因有：您的姓名、开户行、银行卡号等信息不匹配；您当日的提现次数和金额超过限制；您的账户存在异常，被风控拦截。\n","businessCaption":"商务合作","businessContent":"### 品牌合作  <a href=\"mailto:marketing_coop@ele.me\">marketing_coop@ele.me</a>\n\n### 媒体合作   <a href=\"mailto:pr@ele.me\">pr@ele.me</a>","couponCaption":"代金券说明","couponContent":"### Q1: 什么是商家代金券？\n\n商家代金券是指由商家自己发放代金券，只限在指定的商家使用，可根据条件抵扣相应金额。\n\n### Q2: 怎么获得商家代金券？\n\n* 进入有「进店领券」或「下单返券」标示的商家即有机会获得代金券。\n* 「下单返券」需要在指定商家完成满足返券金额要求的订单后才会返还，代金券可在下次下单时使用。\n\n### Q3: 商家代金券使用条件\n\n* 商家代金券仅限在指定商家使用\n* 商家代金券仅限在线支付使用\n* 每个订单只能使用一张商家代金券，不可与其他代金券叠加使用","disclaimerCaption":"免责声明","disclaimerContent":"# Disclaimer:\n\nThe application developed by Ele.me or have. Ele.me makes it clear that, your business activities through the participation of the software have nothing to do with Apple Inc., and Apple does not assume any responsibilities of the program.\n\nMobile phone brand partners from Ele.me, such as Apple and App Store. Sponsors of non-mobile phone clients from Ele.me, nor do you get the mobile phone client’s intellectual property rights or any other rights. If the mobile phone client violates the third party’s intellectual property rights, Ele.me will bears the corresponding legal responsibility.\n\nEle.me has the right but not the obligation to improve or correct any errors or defects.\n\nEle.me will not represent or warrant the accuracy or reliability of any contents,information or advertisements which users obtain from the application. Moreover, Ele.me will not be responsible for any products or information which users purchase by the mobile client. Users shall use the application at their own risk.\n\n# 免责声明：\n\n本应用由饿了么开发或拥有。饿了么在此申明，用户通过本软件参加的商业活动，与Apple Inc.无关，Apple不承担该程序的任何责任。\n\n饿了么手机客户端的品牌合作商，如Apple，AppStore，非饿了么手机客户端的赞助商，也未拥有该客户端的知识产权或其他任何权益。因饿了么手机客户端侵犯第三方知识产权的，由饿了么承担相应的法律责任。\n\n饿了么有权但无义务，改善或更正本手机客户端的任何错误或缺陷。\n\n用户通过饿了么手机客户端及其中的任何链接、下载获得的一切信息、内容或广告，饿了么不声明或保证其正确性或可靠性；并且对于用户经本手机客户端上的展示、内容、广告而购买、取得的任何产品、或信息，饿了么不负保证责任。用户自行承担使用本软件的风险。","discountCaption":"优惠说明","discountContent":"### Q1: 我是新用户，为什么不能享受新用户下单立减优惠／新用户首单红包？\n\n* 新用户是指您的下单设备、手机号、饿了么账号、支付账号都是第一次在饿了么平台使用。以上任意条件不满足，都不能享受新用户优惠。\n* 新用户立减优惠、新用户红包、满减特价菜活动不能同时使用。在您首次下单过程中，我们会优先为您选择优惠最大（优惠金额最高）的。\n\n### Q2: 首单被取消，再次下单还能享受新用户立减优惠／新用户红包吗？\n\n* 您主动取消或商家、骑手取消的订单，饿了么会返还您的新用户资格，这时您仍然可以享受新用户的优惠。\n* 但如果您被取消的首单因为「使用的支付账户曾下过单」，则不能再享受新用户优惠。\n\n\n### Q3: 我第一次在 \"A\" 商家下单，\"A\" 商家有参加「新用户」活动，我为什么享受不到新用户的立减优惠？\n\n* 活动中的新用户指的是饿了么网站的新用户，而不是指 \"A\" 商家的新客户。如果您不符合饿了么网站新用户的定义则无法享受于新用户相关的活动优惠。\n\n### Q4: 一个红包能拆开多次使用吗？\n\n* 不能，一个红包只能一次性使用，不能分开使用。\n\n### Q5: 下单的时候使用了红包，但是后来订单取消了，红包还会返还吗？\n\n* 会的，订单无效后红包会自动返还到您的账户里。\n\n### Q6: 红包兑换码怎样兑换成红包，怎样查看红包？\n\n* 在个人中心 > 我的红包 > 兑换红包，输入兑换码进行兑换。\n\n### Q7: 在线支付订单取消后，钱怎么返还？\n\n* 使用饿了么账户余额支付，订单无效后，所有款项将退到您的饿了么账户；使用第三方支付订单无效后，支付款项将于2个工作日内返还到您的第三方支付账户，红包退还到饿了么账户。\n\n### Q8: 每天限制优惠3单，前面下了一个在线支付的单子，由于未付款，订单自动取消了，这单会计算我的参与活动次数吗？\n\n* 不会。未付款的订单不会计入您使用优惠的次数。\n\n### Q9: 每天红包使用次数有限制吗？\n\n* 同一个手机号每天最多能使用两个红包优惠（不含预订早餐和预订午餐），使用红包的订单出现退单情况不计入限制。\n","forgotCaption":"补签规则","forgotContent":"### Q1:  如何补签？","freshmanCaption":"活动细则","freshmanContent":"* 新用户是根据设备、饿了么账号、下单手机号进行判断，如果您下单时以上信息均为首次使用则符合饿了么定义的新用户\n* 仅限新用户使用移动端app提交订单并选择在线支付时享受（饿配送订单选择餐到付款也可享受）\n* 在本设备上曾经有过购买记录行为的手机不能参加本活动\n* 新用户首单立减金额各区域不同\n* 活动仅支持手机客户端最新版本","hongbaoCaption":"红包问题","hongbaoContent":"### Q1: 怎么获得红包？\n\n* 通过邀请好友获得\n* 在下单返红包的商家下单获得\n* 抢微信朋友分享的红包获得\n\n### Q2: 红包可以做什么？\n\n可以抵扣在线支付时的实际支付金额。\n\n### Q3: 红包的门槛金额按照什么金额计算？\n\n红包的的门槛金额按照（选购菜品折后价＋餐盒费）的金额作为计算门槛，即购物车显示的金额（7.3以上版本）。假设红包满30可用，只要选购的菜品现价（折后价）＋餐盒费超过30，就可以使用红包。\n\n### Q4: 一个红包能拆开多次使用吗？\n\n不能，一个红包只能一次性使用，不能分开使用。\n\n### Q5: 下单的时候使用了红包，但是后来订单取消了，红包还会返还吗？\n\n会的，订单无效后红包会自动返还到您的账户里。\n\n### Q6: 红包兑换码怎样兑换成红包，怎样查看红包？\n\n在个人中心 > 我的红包 > 兑换红包，输入兑换码进行兑换。\n\n### Q7: 邀请好友了，为什么没获得红包？\n\n先检查下您是否在同一设备上进行的邀请？或者被邀请人是否通过您发给对方的链接进行注册？而非自己复制链接注册的。同一设备上邀请或没有按照点击发送的链接邀请都是无效邀请。如果您是正常邀请没获得红包，可以致电客服进行查实。\n\n### Q8: 预订早餐、预订午餐的红包怎么使用？在哪里查询？\n\n* 预订早餐、预订午餐的红包暂时不支持直接点外卖使用，并且当前点外卖的红包也暂时不支持在预订早餐、预订午餐时使用。\n* 预订早餐、预订午餐的红包从APP首页点击“预订早餐”按钮进入后在右上角的个人中心中查找预订早餐、预订午餐的红包。\n\n### Q9: 互斥红包怎么使用？\n\n* 互斥红包不与其他优惠活动（包含但不限于：新用户专享、满X元减X元、满X元赠XX）同时使用。\n\n### Q10: 返红包活动， 红包领取规则是什么?\n\n* 同一个手机号一天限领取两个。\n* 同一注册用户一天限领取两个。\n* 注册用户红包直接发送至用户账户里。\n* 非注册用户红包以兑换码短信的形式发送至用户手机，注册后凭兑换码兑换即可。\n* 红包的使用周期为1周，自红包发放的第2天开始计算。\n* 同一个手机号一天最多使用两个红包。\n* 未收到红包短信的用户注意查看手机里的拦截短信。\n* 有任何问题请拨打客服电话：10105757。","index":"payment,balance,hongbao,pointtext,member,activity,other","memberCaption":"会员问题","memberContent":"### 会员权益调整\n\n尊敬的用户，随着会员服务体系优化，「蜂鸟专送」商家得到大规模普及，大大丰富了会员用户的用餐选择，从2016年9月1日开始，新购及续费的会员服务将由免配送费调整为每单最高减免4元配送费，之前购买的会员服务不受影响，感谢您的支持！敬请期待更多会员特权。\n\n### Q1: 特权介绍\n\n* 减配送费：饿了么会员卡绑定账户，每日前3单在「蜂鸟专送」标识商家可减配送费，每单最高可减4元。\n* 更多特权，敬请期待！\n\n### Q2: 会员资费\n\n* 月卡20元（一个月按31天计算）\n\n### Q3: 购卡说明\n\n* 在线购买：饿了么App > 我的 > 饿了么会员卡。\n* 请认准饿了么官方认证渠道，任何从其他第三方途径获得的会员卡，饿了么不保证其可用性。","memberFullCutCaption":"会员说明","memberFullCutContent":"### Q1: 特权介绍\n\n* 免配送费：饿了么会员卡绑定账户，每日前3单在「蜂鸟专送」标识商家免配送费。\n* 更多特权，敬请期待！\n\n### Q2: 温馨提示\n\n* 用户在当前会员服务失效前，无法购买新卡。\n* 请认准饿了么官方渠道，任何从其他第三方途径获得的会员卡，饿了么不保证其可用性。","memberPartCutCaption":"会员说明","memberPartCutContent":"### Q1: 特权介绍\n\n* 减免配送费： 饿了么会员卡绑定账户，每日前3单在「蜂鸟专送」标识商家可减免配送费，每单最高可减免4元。 \n* 更多特权，敬请期待！\n\n### Q2: 温馨提示\n\n* 用户在当前会员服务失效前，无法购买新卡。\n* 请认准饿了么官方渠道，任何从其他第三方途径获得的会员卡，饿了么不保证其可用性。","memberTimesCaption":"会员说明","memberTimesContent":"> 尊敬的用户，随着会员体系逐渐完善，自2016年10月10日起，饿了么会员权益将做如下优化：\n购卡后31天内，累积享有30单减免配送费服务（每日最多3单，每单最高减免4元）。\n> 注：已购买的会员服务不受影响，当前会员服务失效前无法购买新卡。\n\n### Q1: 特权介绍\n\n* 身份标识：饿了么会员服务有效期内，享有专属皇冠标识。\n* 减免配送费： 饿了么会员卡自绑定账户之日起31天内，在「蜂鸟专送」标识商家下单，享有30次减免配送费特权，每日最多减免3单，每单最高可减4元。\n* 更多特权，敬请期待！\n\n### Q2: 资费介绍\n\n* 饿了么会员卡：20元\n\n### Q3: 使用说明\n\n当用户满足以下任一条件，会员服务自动失效：\n1. 自绑定之日起超过31天；\n2. 在31天内累计使用减免配送费的蜂鸟订单数量达到30单；\n\n### Q4: 购卡说明\n\n* 在线购买：饿了么App>我的>饿了么会员卡\n\n### Q5: 温馨提示\n\n* 用户在当前会员服务失效前，无法购买新卡。\n* 请认准饿了么官方渠道，任何从其他第三方途径获得的会员卡，饿了么不保证其可用性。","ontimeCaption":"准时达问题","ontimeContent":"### Q1: 准时达规则：\n\n饿了么用户在饿了么App带有「准时达」标签的商家下单，若商品送达时间超过承诺送达时间10分钟，我们将为您赔付一个无门槛红包。\n\n### Q2: 关于准时达的特殊情况：\n\na. 预订单暂不参加赔付；\n\nb. 夜间部分时段下单暂不参加赔付；\n\nc. 恶劣天气（雨、雪等）暂不参加赔付；\n\nd. 每位用户每天最多赔付3个红包；\n\n### Q3: 常见问题Q&A\n\nQ1：我怎么收到赔付红包？\n\nA1：当确认送达商品时已超过承诺送达时间10分钟，我们将向您的饿了么账户发放一个无门槛红包，同时会有短信通知到您的手机号码。\n\n### 饿了么保留法律允许范围内对活动的解释权","otherCaption":"其它问题","otherContent":"### Q1: 什么是订单数\n\n订单数是指商家最近30天的订单总数，这个数据是动态的，每天都会进行更新。\n\n### Q2: 商品多久内可以评价？评价后是否能修改？\n\n* 下单确认收货后，7天内可以对商品进行评价。超过7天，评价功能自动失效。\n* 美食一旦点评就不能做修改，评价不可删除撤销，请知悉。\n\n### Q3: 为什么提示下单次数过多，无法下单？\n\n在24小时内，同一手机号在同一设备上最多可以成功提交7次订单。","paymentCaption":"支付问题","paymentContent":"### Q1: 使用余额支付不了？\n\n先确认下您的饿了么账户是否有绑定手机号，使用余额支付必须先绑定手机号。\n\n### Q2: 在线支付订单取消后，钱怎么返还？\n\n使用饿了么账户余额支付（包括：余额、余额+红包），订单无效后，所有款项（包括红包）将退到您的饿了么账户；使用第三方支付（包括：第三方支付、第三方支付+红包），订单无效后，支付款项将于2个工作日内返还到您的第三方支付账户，红包退还到饿了么账户。\n\n### Q3: 在线支付的过程中，订单显示未支付成功，款项却被扣了，怎么办？\n\n该问题属于第三方（银行/支付宝/微信等）数据传输延迟问题。您可以再等待一下， 如果超过半个小时，订单还是未支付状态，第三方会把款项返还到您的付款账户。具体到账时间依银行而定，一般会在2个工作日内。\n\n### Q4: 每天限制优惠3单，前面下了一个在线支付的单子，由于未付款，订单自动取消了，这单会计算我的参与活动次数吗？\n\n不会，您可以在「订单列表页」或者「订单详情页」查看，当订单状态变为「订单已取消」时，系统会自动将您之前享受的优惠返还到您的账户中，下一次下单依然可以使用。自己取消订单、超时未付款自动取消订单、商家取消订单，这些情况都会返还优惠次数。\n\n### Q5: 饿了么账户里的款项怎么提现？\n\n在个人中心 > 我的余额 > 提现处进行提现操作。操作日起2个工作日内，款项会返还至您最近消费过的账户中。\n\n### Q6: 支付宝免密支付功能如何使用？\n\n首次进入支付收银台并使用支付宝支付时，系统会默认进入免密支付签约页面，您可以根据自身支付喜好选择是否签约免密支付功能。如您不签约该功能，之后会默认使用正常支付宝支付流程。此外，您还可以通过“个人中心”－“小额免密支付”中的免密支付开关，随时进行“签约”、“解约”支付宝免密支付操作。","phototutorialCaption":"教我拍大片","phototutorialContent":"![](http://fuss10.elemecdn.com/5/de/5efc9ee81ceba1ae835b0c2298ba9jpeg.jpeg)\n\n## 1. 清晰\n\n拍照时持稳手机，避免出现大幅度晃动。将焦点对准食物本身，锁焦后、点击快门。<br /><br /><br />\n\n![](http://fuss10.elemecdn.com/8/89/4920bc3a0a34c0d26a4460ce03157jpeg.jpeg)\n\n## 2. 光线\n\n尽量选择明亮、柔和的光线，避免强光直射；拍摄时避免身体或器物的阴影遮住食物本身，有条件时，可以用小型手电进行补光，以提高食物质感。<br /><br /><br />\n\n![](http://fuss10.elemecdn.com/4/9b/ca198810b12c8fc57ae562c6fa92cjpeg.jpeg)\n\n## 3. 构图\n\n构图可以选择通用的三分构图法或中心构图法，如果是拍摄整体食物时，需要考虑食物的完整性；如果拍摄特写时，需要将细节体现清楚。<br /><br /><br />\n\n![](http://fuss10.elemecdn.com/c/3b/958696046787bb467708c8d9a5171jpeg.jpeg)\n\n## 4.角度\n\n可以选择45度拍摄、90度俯拍或者水平拍摄，一切都看你的心情哦~<br /><br /><br />\n\n![](http://fuss10.elemecdn.com/2/d3/d31b1f81eb853c6042b9c172a514bjpeg.jpeg)\n\n## 5. 背景\n\n可以选择合适的背景拍摄，例如黑色能体现食物蒸腾的热气，白色让食物更有质感，米色更加温馨等；但背景尽量不要太乱，以免影响整体效果。<br /><br /><br />\n\n![](http://fuss10.elemecdn.com/a/fe/b9716e028917fd81f6c5a99242b6fjpeg.jpeg)\n\n## 6. 细节\n\n餐具的搭配使用、人物的烘托、道具的陪衬等都能使食物更具诱惑力，照片更有氛围。","pointtextCaption":"积分问题","pointtextContent":"### Q1: 怎么获得积分？\n\n在线支付的订单将获得订单积分奖励：\n* 积分将在用户完成评价后获得。\n* 可获得积分=订单金额×10（即1元=10点积分）。\n* 订单金额指实际付款金额，不包含活动优惠金额。\n* 每位用户每天最多可以获得2000积分，体验商家的订单和评价不会增加积分。\n\n### Q2: 积分用来做什么？\n\n可以在积分商城兑换各种礼品。\n\n### Q3: 礼品兑换很多天了还没有收到，该怎么办？\n\n礼品从兑换日起，3个工作日（周末不算）内处理发货，发货后，通常会在3个工作日左右送达。\n\n### Q4: 礼品兑换中的手机充值卡兑换，怎么样进行充值，充值之前会和我电话确认嘛？\n\n不会，手机充值卡兑换，是直接充值到您填写的手机号上，充值之前不会和您电话确认，所以您在填写电话的时候，请确认电话是否正确。","rankCaption":"用户等级说明","rankContent":"用户等级是你在饿了么的身份表现，为了感谢您长期以来对饿了么的信赖和支持，我们会根据你的等级回馈相应福利和特权，功能还在内测中，暂定规则如下：\n\n### 用户等级的划分：\n用户等级按月度计算，每个自然月的最后一天会统计你当月的累计消费金额（限在线支付金额，不包含活动优惠及红包及代金券抵扣），决定你次月的等级，具体要求如下。\n*   Lv0 月累计消费100元以下；\n*   Lv1 月累计消费大于等于100，小于200元\n*   Lv2 月累计消费大于等于200，小于500元\n*   Lv3 月累计消费大于等于500\n\n\n### 升级及降级规则：\n每笔订单完成后，系统会自动计算你当月有效的消费金额（限在线支付金额，不包含活动优惠及红包及代金券抵扣），当月累计消费一旦达到下一等级要求，我们会立刻为您升级，您即时就可享受新等级的相关权益。</br>\n如你本月累计消费金额未达到当前等级要求，次月你将被降到与本月消费相匹配的对应等级。\n\n*内测期间饿了么会调整等级权益及规则，饿了么保留对等级体系的所有解释权","signCaption":"签到规则","signContent":"### Q1: 签到规则"};


